.app-menu-wrapper {
    width: 100%;
    height: 100vh;
    top: 0;
    position: fixed;
    z-index: 5000;
    background: rgba(0,0,0,0.9);
    bottom: 0;
    left: 0;
    right: 0;
}

@-webkit-keyframes menu-appear {
    0% {
        right: -300px;
    }

    100% {
        right: 0;
    }
}

@keyframes menu-appear {
    0% {
        right: -300px;
    }

    100% {
        right: 0;
    }
}

@-webkit-keyframes menu-appear-mobile {
    0% {
        left: -300px;
    }

    100% {
        left: 0;
    }
}

@keyframes menu-appear-mobile {
    0% {
        left: -300px;
    }

    100% {
        left: 0;
    }
}

.app-menu {
    background: #fff;
    height: 100%;
    width: 68%;
    max-width: 255px;
    overflow-y: auto;
    box-sizing: border-box;
    padding: 12px 0 24px;
    position: fixed;
    right: 0;
    -webkit-animation: menu-appear .2s ease-in;
            animation: menu-appear .2s ease-in;
}

.app-menu-section {
    padding: 8px 24px;
    border-top: 1px solid #dcdcdc;
}

.app-menu-section:first-child {
    border-top: 0;
    padding-top: 0;
}

.app-menu-section:last-child {
    border-bottom: 0;
    padding-bottom: 0;
}

.app-menu-section h3 {
    font-size: 12px;
    margin-bottom: 28px;
    text-transform: uppercase;
    font-weight: 500;
    color: #777;
}

.app-menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.app-menu ul li {
    font-size: 14px;
    margin-bottom: 28px;
}

.app-menu-slug {
    padding-top: 28px;
    padding-bottom: 28px;
}

@-webkit-keyframes exit-appear {
    0% {
        opacity: 0;
    }

    15% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes exit-appear {
    0% {
        opacity: 0;
    }

    15% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.app-menu-exit-btn {
    width: 20px;
    height: 20px;
    display: block;
    position: fixed;
    right: 279px;
    top: 0;
    margin: 24px;
    opacity: 1;
    -webkit-animation: exit-appear .3s ease-in-out;
            animation: exit-appear .3s ease-in-out;
    z-index: 5001;
    cursor: pointer;
    outline: none;
}

.app-menu-exit-btn span {
    width: 100%;
    height: 2px;
    background: #fff;
    display: block;
    position: absolute;
    top: 10px;
}

.app-menu-exit-btn span:first-child {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.app-menu-exit-btn span:last-child {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.app-menu-section a {
    color: #111;
    cursor: pointer;
}

.app-menu-version {
    margin-top: 28px;
}

@media screen and (max-width: 640px) {
    .app-menu {
        right: auto;
        left: 0;
        margin: 24px 24px 0;
        height: calc(100% - 24px);
        -webkit-animation-name: menu-appear-mobile;
                animation-name: menu-appear-mobile;
    }

    .app-menu-exit-btn {
        right: 0;
    }
}

.login-bar {
