    overflow: hidden;
    height: 56vw;
    display: flex;
    box-sizing: border-box;
    margin-top: 105px;
    position: relative;
    justify-content: center;
}

.resource-video-container {
    width: 100%;
    overflow: hidden;
    height: 0;
    padding-bottom: 56%;
    display: flex;
    box-sizing: border-box;
    position: relative;
    justify-content: center;
}

.video-container .video-play-icon-button {
    z-index: 1000;
    position: absolute;
    top: 0;
    width: 120px;
    height: 120px;
    display: block;
    margin: calc((56vw / 2) - 60px) auto 0;
    cursor: pointer;
}

.video-container .video-play-icon-button svg {
    fill: #fff;
    width: 100%;
    height: 100%;
}

.video-container iframe,
.resource-video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.video-page-card-wrapper {
    position: relative;
    transition: all 0.3s ease-in-out;
    top: -20vw;
    margin-bottom: 14px;
}

.video-page-card-wrapper.playing {
    top: 0;
    transition: all 0.3s ease-in-out;
    margin-bottom: 200px;
}

.video-page-card-wrapper .page-card {
    top: 0;
}

.video-series-meta {
    max-width: 700px;
    margin-right: auto;
    margin-left: auto;
}

.video-meta p {
    margin-bottom: 0 !important;
}

.video-meta p:last-child {
    margin-top: 0 !important;
}

.video-image-blur {
    width: 100%;
    display: block;
    position: absolute;
    top: 0;
    z-index: 200;
    height: 100%;
}

.video-image-blur img,
.video-image-blur span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    object-fit: cover;
}

.video-scrollable {
    position: absolute;
    z-index: 199;
    background: transparent;
    display: block;
    bottom: 65px;
    top: 0;
    width: 100%;
}

.video-scrollable.top {
    top: 0;
    height: 20vw;
    width: 100%;
    left: 0;
}

.video-scrollable.bottom {
    bottom: 65px;
    height: 20vw;
    width: 100%;
    left: 0;
}

.video-scrollable.left {
    bottom: 65px;
    top: 0;
    width: 30%;
    left: 0;
}

.video-scrollable.right {
    bottom: 65px;
    top: 0;
    width: 30%;
    right: 0;
}

@media screen and (max-width: 968px) {
    .video-scrollable.bottom {
        height: 10vw;
    }

    .video-page-card-wrapper {
        margin-bottom: 50px;
    }
}

@media screen and (max-width: 640px) {
    .video-container .video-play-icon-button {
        width: 60px;
        height: 60px;
        margin: 3rem auto 0;
    }
}

